import { Box, colors, Stack, Tooltip, Typography } from "@mui/material";
import React from "react";
import { useParams } from "react-router-dom";
import { arabicNumbers, surah } from "../../data/surah";
// import ArrowDropUpIcon from "@mui/icons-material/ArrowDropUp";
import NorthIcon from "@mui/icons-material/North";

export default function StudenPage() {
  const { studentId: studentIdStr } = useParams();
  const studentId = parseInt(studentIdStr);

  const start = 67;
  const end = 78;

  const commulative = surah.slice(start - 1, end - 1).reduce((acc, item) => {
    return [
      ...acc,
      {
        ...item,
        ayahSum:
          acc.length > 0
            ? acc[acc.length - 1].ayahSum + acc[acc.length - 1].ayah
            : 0,
      },
    ];
  }, []);

  console.log("commulative", commulative);

  const ranges = [
    {
      from: { id: 67, ayah: 15 },
      to: { id: 70, ayah: 20 },
    },
    {
      from: { id: 73, ayah: 1 },
      to: { id: 75, ayah: 40 },
    },
  ];

  const commulativeRanges = ranges.map((range) => {
    return {
      from:
        commulative.find((item) => item.id === range.from.id).ayahSum +
        range.from.ayah,
      to:
        commulative.find((item) => item.id === range.to.id).ayahSum +
        range.to.ayah,
    };
  });

  const total =
    commulative[commulative.length - 1].ayahSum +
    commulative[commulative.length - 1].ayah;

  console.log("ranges", ranges);
  console.log("commulativeRanges", commulativeRanges);
  console.log("total", total);

  return (
    <Box width="100%">
      <Typography variant="h4" align="center">
        الطالبة {studentId}
      </Typography>

      <Box
        minWidth="100%"
        minHeight={50}
        mt={5}
        sx={{
          bgcolor: colors.grey[200],
          border: `1px solid ${colors.grey[400]}`,
        }}
        position="relative"
      >
        {commulativeRanges.map((range, index) => (
          <Tooltip
            key={range.from}
            title={`أتمت الطالبة المراجعة من ${
              surah.find(({ id }) => id === ranges[index]?.from?.id)?.surah
            }`}
          >
            <Box
              key={range.from}
              bgcolor={colors.teal["A700"]}
              position={"absolute"}
              left={(range.from / total) * 100 + "%"}
              width={((range.to - range.from) / total) * 100 + "%"}
              minHeight={50}
            >
              <NorthIcon
                sx={{
                  position: "absolute",
                  bottom: -25,
                  left: -10,
                }}
              />
              <Stack
                sx={{
                  position: "absolute",
                  bottom: -85,
                  transform: "translateX(-50%)",
                }}
                justifyContent="center"
                alignItems="center"
              >
                <Box
                  sx={{
                    backgroundImage: 'url("/tejan-alnoor/ayahIcon.png")',
                    backgroundSize: "28px 30px",
                    backgroundRepeat: "no-repeat",
                    backgroundPosition: "center",
                    width: 30,
                    height: 30,
                  }}
                  justifyContent="center"
                  alignItems="center"
                >
                  <Typography
                    variant="subtitle1"
                    component="div"
                    sx={{
                      width: "fit-content",
                      lineHeight: "35px",
                      mx: "auto",
                    }}
                  >
                    {ranges[index]?.from?.ayah
                      .toString()
                      .split("")
                      .map((number) => arabicNumbers[number])
                      .join("")}
                  </Typography>
                </Box>
                <Typography lineHeight={1} sx={{ mt: 1 }}>
                  {
                    surah.find(({ id }) => id === ranges[index]?.from?.id)
                      ?.surah
                  }
                </Typography>
              </Stack>
            </Box>
          </Tooltip>
        ))}
      </Box>
    </Box>
  );
}
